.title {
  font-size: 24px;
  margin: 15px;
}

.page_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.grid_container {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(auto-fill, 230px);
  justify-content: center;
  gap: 20px;
}
